<template>
  <v-container>
    <v-row>
      <v-col class="mb-4">
        <h1 class="display-2 font-weight-bold mb-3">
          Edición de Campañas
        </h1>
      </v-col>
    </v-row>
    <v-row>
      <v-col
        cols="12"
        md="8"
      >
        <form @submit.prevent="guardarCampania()">
          <v-text-field
            v-model="campaign.name"
            label="Campaña"
            outlined
            prepend-icon="mdi-label"
            required
          />
          <v-text-field
            v-model="campaign.descripcion"
            label="Oferta"
            outlined
            prepend-icon="mdi-label"
            required
          />
          <v-text-field
            v-model="campaign.bono"
            label="Bono"
            type="number"
            outlined
            prepend-icon="mdi-cash"
            prefix="$"
            required
          />
          <v-text-field
            v-model="campaign.startDate"
            label="Fecha Inicio"
            type="date"
            outlined
            prepend-icon="mdi-calendar"
            required
          />
          <v-text-field
            v-model="campaign.endDate"
            label="Fecha Fin"
            prepend-icon="mdi-calendar"
            type="date"
            outlined
            required
          />

          <v-card-actions>
            <v-btn
              to="/ventas"
              color="blue-grey"
              class="mr-4"
            >
              Cancelar
            </v-btn>
            <v-btn
              type="submit"
              color="green"
              class="mr-4"
            >
              Actualizar
            </v-btn>
          </v-card-actions>
        </form>
      </v-col>
    </v-row>
  </v-container>
</template>
<script>
  import axios from 'axios'

  const urlCampaña = 'https://sistema-adv.herokuapp.com/api/campaign/'

  export default {
    name: 'EditarCampania',
    data () {
      return {
        campaign: {
          name: '',
          descripcion: '',
          bono: '',
          startDate: '',
          endDate: '',
        },
      }
    },
    mounted () {
      this.activarEdicion()
    },
    methods: {
      activarEdicion () {
        this.id = this.$route.params.id
        axios.get(urlCampaña + this.id).then(res => {
          this.campaign = res.data
        }).catch(e => { console.log(e) })
      },
      guardarCampania () {
        const router = this.$router
        const params = this.campaign

        axios.put(urlCampaña + this.id, params).then((res) => {
          // console.log(res);
          router.push('/ventas/campañas')
        })
      },
    },
  }
</script>
